import React from 'react';

import './App.css';
import Game from './components/Game';
import Welcome from './components/Welcome';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import { Component } from 'react';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      player1: '',
      player2: ''
    };
    this.insertName = this.insertName.bind(this);
  }

  insertName(n1, n2) {
    this.setState({ player1: n1, player2: n2 });
  }
  render() {
    return (
      <div id="App">
        <BrowserRouter basename="/bingo">
          <Switch>
            <Route
              exact
              path="/"
              render={routeProps => (
                <Welcome {...routeProps} onInsert={this.insertName}></Welcome>
              )}
            ></Route>
            <Route
              path="/game"
              render={routeProps => (
                <Game
                  player1={this.state.player1}
                  player2={this.state.player2}
                  {...routeProps}
                ></Game>
              )}
            ></Route>
          </Switch>
        </BrowserRouter>
      </div>
    );
  }
}

export default App;
